import logo from "./logo.svg";
import "./App.css";
import { useRef } from "react";
import { encode as _encode } from "iconv-lite";

function App() {
  const inputRef = useRef();
  const remoteDevice = useRef();
  function handlePrint() {
    let d = remoteDevice.current;
    let setKorean = new Uint8Array([0x1b, 0x52, 0x0d]);
    let cut = new Uint8Array([0x08, 0x56, 0x42, 0xff, 0x1b, 0x40]);

    d.transferOut(2, setKorean)
      .then(() => d.transferOut(2, _encode(inputRef.current.value, "EUC-KR")))
      .then(() => d.transferOut(2, cut));
  }
  function handleConnect() {
    navigator.usb
      .requestDevice({ filters: [] })
      .then((selectedDevice) => {
        remoteDevice.current = selectedDevice;
        return remoteDevice.current.open();
      })
      .then(() => remoteDevice.current.selectConfiguration(1))
      .then(() => remoteDevice.current.claimInterface(0));
  }

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <textarea ref={inputRef} style={{ width: "100%", height: 150 }} />
        <button onClick={handlePrint}>print</button>
        <button onClick={handleConnect}>connect</button>
      </header>
    </div>
  );
}

export default App;
